    next() {
      if (!this.radio) {
        this.$toast("请选择答案");
      } else {
        if (this.index == 39) {
          this.index = 0;
          this.radio = this.answer[this.index];
        } else {
          this.index++;
          this.radio = this.answer[this.index];
        }
        this.question = this.list[this.index]
      }
    },
    previous() {
      if (!this.radio) {
        this.$toast("请选择答案");
      } else {
        if (this.index == 0) {
          this.index = 39;
          this.radio = this.answer[this.index];
        } else {
          this.index--;
          this.radio = this.answer[this.index];
        }
        this.question = this.list[this.index]
      }
    },
    submit(){
        let i = 0;
        this.answer.forEach(function(a,b){
            if(a){
                i++
            }
        })
        this.number = i
        Dialog.confirm({
            message: '当前答题进度为'+this.number+'/40,确认提交?'
        }).then(() => {
            this.show = true
            this.upload()
        }).catch(() => {
            
        })
    },
    upload(){
        setTimeout(()=>{
            this.show = false
            this.$router.push('result')
        },2000)
    }
  },
  mounted() {
    this.question = this.list[0]
  }
};
</script>
